
import TwigComponentpropername from "./proper-name/proper-name.js";
customElements.get("proper-name") || customElements.define("proper-name", TwigComponentpropername);
customElements.get("proper-name").template = "<style>.name {\n  color: black; }\n\n.suffix {\n  color: darkgray;\n  font-style: italic; }\n</style>\n<span class=\"name\">{{ first }} {{ last }}</span>{% if suffix %}<span class=\"suffix\">, {{ suffix }}</span>{% endif %}\n"
import TwigComponentsmartheader from "./smart-header/smart-header.js";
customElements.get("smart-header") || customElements.define("smart-header", TwigComponentsmartheader);
customElements.get("smart-header").template = "<style></style>\n<h{{ depth }}>\n  <slot></slot>\n</h{{ depth }}>\n"


